.snakeBTN a {
    text-align: center;
    background: transparent;
    color: #96DFDB;
    margin: 1vw;
    -webkit-transition: 0.5s;
    transition: 0.5s;
}
.snakeBTN a span h6 {
    color: #96DFDB;
    font-size: 3vw;
}
.snakeBTN a span {
    position: absolute;
    -webkit-transition: -webkit-transform .5s;
    transition: -webkit-transform .5s;
    transition: transform .5s;
    transition: transform .5s, -webkit-transform .5s;

}
/* Horizontal Snake Movement */
.snakeBTN a span:nth-child(1),
.snakeBTN a span:nth-child(3){
    width: 100%;
    height: 1px;
    background: #96DFDB;
}
/* Top */
.snakeBTN a span:nth-child(1){
    top: 0;
    left: 0;
    -webkit-transform-origin: right;
            transform-origin: right;
}
.snakeBTN a:hover span:nth-child(1){
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
    -webkit-transform-origin: left;
            transform-origin: left;
    -webkit-transition: -webkit-transform .5s;
    transition: -webkit-transform .5s;
    transition: transform .5s;
    transition: transform .5s, -webkit-transform .5s;
}
/* Bottom */
.snakeBTN a span:nth-child(3){
    bottom: 0;
    left: 0;
    -webkit-transform-origin: left;
            transform-origin: left;
}
.snakeBTN a:hover span:nth-child(3){
    -webkit-transform: scaleX(0);
            transform: scaleX(0);
    -webkit-transform-origin: right;
            transform-origin: right;
    -webkit-transition: -webkit-transform .5s;
    transition: -webkit-transform .5s;
    transition: transform .5s;
    transition: transform .5s, -webkit-transform .5s;
}
/* Verticle Snake Movement */
.snakeBTN a span:nth-child(2),
.snakeBTN a span:nth-child(4){
    width: 1px;
    height: 100%;
    background: #96DFDB;
}
/* Left */
.snakeBTN a span:nth-child(2){
    top: 0;
    left: 0;
    -webkit-transform: scaleY(0);
            transform: scaleY(0);
    -webkit-transform-origin: bottom;
            transform-origin: bottom;
}
.snakeBTN a:hover span:nth-child(2){
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
    -webkit-transform-origin: top;
            transform-origin: top;
    -webkit-transition: -webkit-transform .5s;
    transition: -webkit-transform .5s;
    transition: transform .5s;
    transition: transform .5s, -webkit-transform .5s;
}
/* Right */
.snakeBTN a span:nth-child(4){
    top: 0;
    right: 0;
    -webkit-transform: scaleY(0);
            transform: scaleY(0);
    -webkit-transform-origin: top;
            transform-origin: top;
}
.snakeBTN a:hover span:nth-child(4){
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
    -webkit-transform-origin: bottom;
            transform-origin: bottom;
    -webkit-transition: -webkit-transform .5s;
    transition: -webkit-transform .5s;
    transition: transform .5s;
    transition: transform .5s, -webkit-transform .5s;
}


:root{
    --primary-color: rgba(100%, 100%, 100%, 0.85);
    --overlay-color:rgb(24%, 58%, 58%, 0.9);
    --quote-color: rgb(24%, 58%, 58%, 0.50);
}
.banner{
    overflow: scroll;
    background-image: url(/MariaAzam/static/media/BG.84c1eb74.png);
    background-repeat: no-repeat;
    background-size: auto;
    background-position: bottom right;
    height: 100vh;
    width: 100%;
}
.s-line, .m-line{
    height: 4px;
    width: 30px;
    margin: 7px;
}
.s-line{
    background: #54B6B7
}
.m-line{
    background-color: #BB6275;
}
.burger{
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    -webkit-transition: all 0.4s;
    transition: all 0.4s;
}
.toggle{
    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;
    cursor: pointer;
    width: 30px;
    height: 30px;
    opacity: 0;
}

/* Animate Toggle */
 .toggle:checked + .burger{
     margin-top: 10px;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
 }
 .toggle:checked + .burger .s-line:nth-child(3){
     margin: 0px;
     bottom: 0;
     -webkit-transform: rotate(45deg);
             transform: rotate(45deg);
 }
 .toggle:checked + .burger .s-line:nth-child(1){
    display: none;

 }
 .toggle:checked + .burger .m-line{
    margin: 0px;
    -webkit-transform: rotate(135deg);
            transform: rotate(135deg);
}
.toggle:checked ~ .mainContent{
    display: none;
}

/* Animate Menu */
.toggle:checked ~ .menu {
    visibility: visible;
}
.toggle:checked ~ .menu .transition{
    -webkit-transform: scale(1);
            transform: scale(1);
    -webkit-transition-duration: 0.4s;
            transition-duration: 0.4s

}
.toggle:checked ~ .mainContent{
    z-index: -10;
}

/* Menu Overlay */
.menu{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    visibility: hidden;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
}
.transition{
    background-color: rgb(24%, 58%, 58%, 0.9);
    background-color: var(--overlay-color);
    border-radius: 50%;
    width: 200vw;
    height: 200vw;
    display: flex;
    flex: none;
    align-items: center;
    justify-content: center;
    -webkit-transform: scale(0);
            transform: scale(0); 
    -webkit-transition: all 0.4s ease-in-out; 
    transition: all 0.4s ease-in-out;
}

